.welcome-head {
  text-align: center;
  color: #FF6C54;
  padding-top: 20px;
}

.welcome-text {
  font-size: 18px;
  text-align: center;
  color: #FFA319;
  padding-bottom: 20px;
}

.welcome-footer {
  font-size: 12px;
  text-align: center;
  color: #FFA319;
  padding-bottom: 20px;
}
.image-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 0 10px;
  grid-auto-rows: 10px;
}

.image-list img {
  width: 250px;
  grid-row-end: span 2;
  box-shadow: .5px 0px 5px #888;
}
